/*
 * custom properties
 */
:root {
  /* primary palette */
  --gr-primary-blue: #028abe;
  --gr-dark-blue: #182962;
  --gr-light-orange: #ffb180;
  --gr-orange: #f47c2e;
  --gr-dark-orange: #ff6400;
  --gr-red: #ce0000;
  --gr-green: #98cb4e;
  --gr-light-gray: #ebeced;
  --gr-medium-gray: #ccc;
  --gr-dark-gray: #979797;
  --gr-black: #000;
  --gr-white: #fff;
  --gr-alert-major-color: #ce0000;
  --gr-alert-minor-color: #f47c2e;
  /* sizes */
  --gr-component-min-width: 1440px;
  --gr-component-max-width: 1440px;
}

/*
 * fonts
 */
@font-face {
  font-family: "gr-icon-set";
  font-style: normal;
  font-weight: normal;
  src: url("../../assets/fonts/gr-icon-set.eot");
  src: url("../../assets/fonts/gr-icon-set.eot?#iefix") format("embedded-opentype"), url("../../assets/fonts/gr-icon-set.ttf") format("truetype"), url("../../assets/fonts/gr-icon-set.woff") format("woff"), url("../../assets/fonts/gr-icon-set.svg#svggr-icon-set") format("svg");
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
}
/*
 * reset
 */
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

header,
footer,
nav,
section,
article,
figure,
picture,
iframe {
  display: block;
}

html,
body,
div,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
ol,
ul,
li,
form,
legend,
label,
table,
header,
footer,
nav,
section,
figure {
  padding: 0;
  margin: 0;
}

/*
 * fieldset
 */
fieldset {
  padding: 0;
  border: 0;
  margin: 0 0 .5rem;
}
fieldset:last-child {
  margin: 0;
}

/*
 * hr
 */
hr {
  background-color: #ccc;
  background-color: var(--gr-medium-gray);
  border: 0;
  height: 1px;
  margin: 0 auto 1rem;
  max-width: calc(1440px - 2rem);
  /* this value should be in a variable */
}

[class^="gr-i-"], [class*=" gr-i-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'gr-icon-set' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

