/*
 * label
 */
label {
  display: table;
  /* prevents element from spanning width of available space */
  color: var(--label-text-color);
  font-weight: normal;
  margin: 0 0 .5rem;
}

/*
 * list
 */
ul,
ol {
  padding: 0 0 0 2rem;
}

/* NOTE: target li elements whose parent ul/ol elements don't have a class attribute -- targeting this way prevents specificity issues */
/*
 * media
 */
img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
}
a img {
  border: 0;
}

/*
 * select
 */
/*
 * table
 */
table {
  width: 100%;
  border: 0;
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0 0 1rem;
  /* bottom margin should be in variable */
}

caption {
  font: italic normal 400 100%/1.5 "Roboto", sans-serif;
  font-size: 12px;
}

.table-heading,
th {
  color: #000;
  color: var(--gr-black);
  text-align: left;
  padding: 0;
  margin: 0 0 10px;
}

th,
td {
  padding: .75rem 1rem;
  border-width: 0 0 1px;
  border-style: solid;
  text-align: left;
}

th,
thead td {
  border-color: #000;
  border-color: var(--gr-black);
  font-weight: bold;
  vertical-align: bottom;
}

td {
  border-color: #ccc;
  border-color: var(--gr-medium-gray);
  vertical-align: top;
}

/* zebgr-striped rows */
/*
 * text
 */
a {
  color: #028abe;
  color: var(--gr-primary-blue);
  transition: all 0.1s ease-in-out;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

p {
  margin: 0 0 1.3rem;
}

blockquote {
  border-left-color: #ccc;
  border-left-color: var(--gr-medium-gray);
  border-left-style: solid;
  border-left-width: 2px;
  margin: 1em 0 1em 2em;
  padding: 0 0 0 1em;
}

small {
  font-size: 12px;
}

/* prevent 'sup' and 'sub' elements from affecting line-height 
   https://css-tricks.com/snippets/css/prevent-superscripts-and-subscripts-from-affecting-line-height */
sup,
sub {
  vertical-align: baseline;
  position: relative;
  top: -0.4em;
}

sub {
  top: 0.4em;
}

abbr {
  text-decoration: none;
  border-bottom-width: 1px;
  border-bottom-style: solid;
}

mark,
cite,
kbd,
code,
samp,
var,
ins,
dfn {
  background-color: #ccc;
  background-color: var(--gr-medium-gray);
  padding: .25em .5em;
}

ins {
  text-decoration: none;
}

pre {
  margin: 0;
}

/*
 * text input
 */
input[type=text],
input[type=search],
input[type=url],
input[type=number],
input[type=date],
input[type=email],
input[type=password],
textarea,
.gr-c-input-text {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
  border-style: solid;
  border-width: 1px;
  /* this value should be in variable -- same number used for <select>, checkbox and radio */
  font: normal normal 400 100%/1.5 "Open Sans", sans-serif;
  height: 2.875rem;
  width: 100%;
  padding: .6rem .75rem;
  outline: none;
  /* remove/reset platform-specific styling, like Webkit on mobile */
  border-radius: 0;
  appearance: none;
  /* remove IE's "clear" button */
}
input[type=text]:hover,
input[type=search]:hover,
input[type=url]:hover,
input[type=number]:hover,
input[type=date]:hover,
input[type=email]:hover,
input[type=password]:hover,
textarea:hover,
.gr-c-input-text:hover {
  border-color: #979797;
  border-color: var(--gr-dark-gray);
}
input[type=text]:focus,
input[type=search]:focus,
input[type=url]:focus,
input[type=number]:focus,
input[type=date]:focus,
input[type=email]:focus,
input[type=password]:focus,
textarea:focus,
.gr-c-input-text:focus {
  border-color: #028abe;
  border-color: var(--gr-primary-blue);
}
input[type=text]::-ms-clear,
input[type=search]::-ms-clear,
input[type=url]::-ms-clear,
input[type=number]::-ms-clear,
input[type=date]::-ms-clear,
input[type=email]::-ms-clear,
input[type=password]::-ms-clear,
textarea::-ms-clear,
.gr-c-input-text::-ms-clear {
  display: none;
}

textarea,
textarea.gr-c-input-text {
  display: block;
  /* removes unnecessary space below element */
  min-height: 100px;
  line-height: 1.4;
  overflow: auto;
  /* without this, scrollbar placeholder is visible in IE 11- */
  resize: vertical;
}

input[type=file] {
  width: 100%;
  /* NOTE: this input type had an explicit width by default; using "100%" allows it to to be squishy */
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* NOTE: Hide Webkit-added pseudo */
/*       http://geek.michaelgrace.org/2011/06/webkit-seargr-input-styling/ */
input::-webkit-search-decoration,
input::-webkit-search-cancel-button {
  display: none;
}

/* target IE 10 and 11 */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /* in IE 11, without setting the line-height, the text seems to be closer to the bottom edge of the text input */
  input[type=text],
  input[type=search],
  input[type=url],
  input[type=number],
  input[type=date],
  input[type=email],
  input[type=password],
  textarea,
  .gr-c-input-text {
    line-height: 1;
  }
}
/* target firefox */
::-moz-placeholder {
  /* firefox decreases placeholder opacity by default, so reset back to 1 */
  opacity: 1;
}

/*
 * main
 */
html {
  font-size: 1rem;
  background-color: #fff;
  /* temporary "sticky" footer solution -- keep until non-header/non-footer components are prototyped */
  height: 100%;
}

body {
  color: #000;
  color: var(--gr-black);
  font: normal normal 400 100%/1.5 "Open Sans", sans-serif;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  /* NOTE: text-size-adjust (non-standard) -- https://developer.mozilla.org/en-US/docs/Web/CSS/text-size-adjust */
  text-size-adjust: 100%;
  /* NOTE: font smoothing (non-standard) -- https://developer.mozilla.org/en-US/docs/Web/CSS/font-smooth */
  -webkit-font-smoothing: antialiased;
  /* hide horizontal overflow -- prevents mega menu from adding horizontal scrollbar */
  overflow-x: hidden;
}

.gr-l-page,
.rl-l-template {
  /* temporary min-width -- use until we get into responsiveness */
}

.gr-l-main {
  padding: 2rem 0 4rem;
  position: relative;
  /* "relative" is necesssary here -- provides constraint for "__content" element's pseudo element  */
  z-index: 1;
  /* this element should be lower than the "gr-l-main" element */
  flex: 1;
  /* helps keep footer "sticky" */
  /* browser fallbacks */
  /* In IE 11, the solution for the "sticky footer" is causing the footer to overlap other content. Reset "flex" property on ".gr-l-main" element to resolve. Footer will not be sticky in IE 11. */
}
.gr-l-main__inner {
  margin: auto;
  display: flex;
}
.gr-l-main__inner--stacked {
  flex-direction: column;
}
.gr-l-main__inner--split {
  flex-direction: row;
  max-width: calc(1440px + 2rem);
  /* value takes padding into account -- this value should be in a variable */
  padding: 0 1rem;
  margin: auto;
}
.gr-l-main__inner--split .gr-l-section {
  margin: 0 0 2rem;
}
.gr-l-main__inner--split .gr-l-section--no-margin {
  margin: auto;
}
.gr-l-main__inner--split .gr-l-section__inner {
  padding: 0;
}
.gr-l-main__side {
  position: absolute;
  z-index: 1;
  left: -100%;
  transition: all 300ms ease-out;
  /* speed value should be in variable -- shared with Slinky nav */
  width: calc(100% - 2rem);
}
.gr-l-main__side--active {
  left: 1rem;
}
.gr-l-main__side--active + .gr-l-main__content:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  /* this value should be in a variable */
}
.gr-l-main__content {
  width: 100%;
}
.gr-l-main__content--inactive .gr-l-main__content-inner {
  transform: scale(0.95);
  /* this value should be in a variable */
}
.gr-l-main__content--inactive:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: transparent;
  transition: all 300ms ease-out;
  /* speed value should be in variable -- shared with Slinky nav */
}
.gr-l-main__content-inner {
  transition: all 300ms ease-out;
  /* speed value should be in variable -- shared with Slinky nav */
  transform-origin: top center;
}
.gr-l-main--w-bg {
  background: #ebeced;
}
@media (min-width: 48em) {
  .gr-l-main__side, .gr-l-main__side--active {
    position: relative;
    transition: none;
    left: auto;
    flex: 0 0 26%;
    margin: 0 2rem 0 0;
  }
  .gr-l-main__side + .gr-l-main__content:after, .gr-l-main__side--active + .gr-l-main__content:after {
    display: none;
  }
  .gr-l-main__content, .gr-l-main__content--inactive {
    transition: none;
  }
  .gr-l-main__content:after, .gr-l-main__content--inactive:after {
    display: none;
  }
  .gr-l-main__content-inner {
    transform: none;
  }
}
.gr-mod-no-cssgrid .gr-l-main {
  flex: auto;
}

/*
 * section
 */
.gr-l-section {
  width: 100%;
  margin: 0 0 3rem;
}
.gr-l-section__inner {
  max-width: calc(1440px + 2rem);
  /* value takes padding into account -- this value should be in a variable */
  padding: 0 1rem;
  margin: auto;
}
.gr-l-section:last-child {
  margin: 0;
}
.gr-l-section--full {
  max-width: 100%;
  background-color: #ebeced;
}
.gr-l-section--full .gr-l-section__inner {
  padding: 0;
}
.gr-l-section--no-margin {
  margin: auto;
}

/*
 * wrapper
 */
.gr-l-wrapper {
  /* browser fallbacks */
  /* In IE 11, the split items (60/40 and 40/60 children) are wrapping unnecessarily. Rather than padding the childrem, pad the children of children. Also, override flex-wrap value. */
}
.gr-l-wrapper__item {
  width: 100%;
}
.gr-l-wrapper--60-40 {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.gr-l-wrapper--60-40 .gr-l-wrapper__item {
  flex: 0 0 100%;
}
.gr-l-wrapper--60-40 .gr-l-wrapper__item:first-child {
  margin: 0 0 3rem;
}
@media (min-width: 80em) {
  .gr-l-wrapper--60-40 .gr-l-wrapper__item:first-child {
    width: 60%;
    /* this seems to resolve flexbox issue with "gr-c-carousel" component */
    flex: 0 0 60%;
    padding-right: 3rem;
    /* this value should be in a variable */
    margin: 0;
  }
  .gr-l-wrapper--60-40 .gr-l-wrapper__item:last-child {
    width: 40%;
    flex: 0 0 40%;
  }
}
.gr-l-wrapper--40-60 {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.gr-l-wrapper--40-60 .gr-l-wrapper__item {
  flex: 0 0 100%;
}
.gr-l-wrapper--40-60 .gr-l-wrapper__item:first-child {
  margin: 0 0 3rem;
}
@media (min-width: 80em) {
  .gr-l-wrapper--40-60 .gr-l-wrapper__item:first-child {
    width: 40%;
    /* this seems to resolve flexbox issue with "gr-c-carousel" component */
    flex: 0 0 40%;
    padding-right: 3rem;
    /* this value should be in a variable */
    margin: 0;
  }
  .gr-l-wrapper--40-60 .gr-l-wrapper__item:last-child {
    width: 50%;
    flex: 0 0 60%;
  }
}
.gr-mod-no-cssgrid .gr-l-wrapper {
  flex-wrap: nowrap;
}
.gr-mod-no-cssgrid .gr-l-wrapper .gr-l-wrapper__item:first-child {
  padding-right: 0;
}
.gr-mod-no-cssgrid .gr-l-wrapper .gr-l-wrapper__item:first-child > * {
  padding-right: 3rem;
  /* this value should be in a variable */
}

/*
 * grid
 */
.gr-l-grid {
  display: grid;
}
.gr-l-grid--demo {
  grid-gap: 1rem;
  grid-template-columns: minmax(0, 1fr) 50%;
  padding: 1rem;
  border: 1px solid #ccc;
}
.gr-l-grid--demo__item {
  text-align: center;
  text-transform: uppercase;
  padding: 1rem;
  border: 1px solid #ccc;
}

.dk-select,
.dk-select *,
.dk-select *:before,
.dk-select *:after,
.dk-select-multi,
.dk-select-multi *,
.dk-select-multi *:before,
.dk-select-multi *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.dk-select,
.dk-select-multi {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  line-height: 1.5em;
  width: 200px;
  cursor: pointer;
  font-size: 18px;
  /* addition */
}

.dk-selected {
  color: #028abe;
  color: var(--gr-primary-blue);
  /* addition */
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  position: relative;
  background-color: white;
  background: #fff;
  /* override */
  border: 1px solid #028abe;
  border-radius: 0;
  padding: 0 .75rem;
  /* override -- helps maintain consistent padding with text inputs */
  line-height: 46px;
  /* addition -- magic number, matches comp */
  font-family: "Open Sans", sans-serif;
  /* addition -- helps maintain consistent font with text inputs */
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
}
.dk-selected:before, .dk-selected:after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
}
.dk-selected:before {
  /* override */
  top: 1.2rem;
  right: 1rem;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #028abe;
  /* end override */
}
.dk-selected:after {
  top: 0;
  height: 100%;
  margin: 0 1.5em 0 0;
}

.dk-selected-disabled {
  color: #ccc;
}

.dk-select .dk-select-options {
  position: absolute;
  display: none;
  left: 0;
  right: 0;
}

.dk-select-open-up .dk-select-options {
  border-radius: 0 0 0 0;
  margin-bottom: -1px;
  bottom: 100%;
}

.dk-select-open-down .dk-select-options {
  border-radius: 0 0 0 0;
  margin-top: -1px;
  /* update */
  top: 100%;
}

.dk-select-multi .dk-select-options {
  max-height: 10em;
}

.dk-select-options {
  background-color: white;
  border: 1px solid #028abe;
  border-radius: 0;
  list-style: none;
  margin: 0;
