/*
 * primary nav
 */
.gr-c-primary-nav {
  background-color: #182962;
  background-color: var(--gr-dark-blue);
  /* should menu element be maintained in separate partial? */
}
.gr-c-primary-nav__list {
  display: flex;
  list-style: none;
  max-width: 1440px;
  padding: 0 2rem;
  /* the side padding should be in a variable since it's widely used */
  margin: auto;
}
.gr-c-primary-nav__link {
  display: block;
  font-size: 26px;
  color: #fff;
  text-transform: uppercase;
  padding: 1.5rem 1rem;
  white-space: nowrap;
}
.gr-c-primary-nav__link:hover, .gr-c-primary-nav__link--active {
  background-color: #028abe;
  text-decoration: none;
}
.gr-c-primary-nav__menu {
  background-color: #ebeced;
  padding: 1rem;
}
.gr-c-primary-nav__menu-inner {
  max-width: 1440px;
  padding: 1rem 0;
  margin: auto;
  /* grid fallback */
  display: flex;
  flex-wrap: wrap;
}
@supports (display: grid) {
  .gr-c-primary-nav__menu-inner {
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
  }
}
.gr-c-primary-nav__col {
  /* grid fallback */
  flex: 0 0 25%;
  min-width: 300px;
  margin-bottom: 2rem;
  /* matches grid gap */
}
@supports (display: grid) {
  .gr-c-primary-nav__col {
    margin-bottom: 0;
    /* overrides grid fallback value */
  }
}
.gr-c-primary-nav__menu-box {
  margin: 0 0 2rem;
}
.gr-c-primary-nav__menu-box:last-child {
  margin: 0;
}
.gr-c-primary-nav__menu-heading {
  font-size: 22px;
  /* this value should be in a variable */
  font-weight: bold;
  line-height: 1.2;
  /* should this be global? */
  color: #f47c2e;
  padding: 0 0 1.25rem;
  margin: 0 0 1rem;
  position: relative;
}
.gr-c-primary-nav__menu-heading:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 5rem;
  height: .25rem;
  background: #028abe;
}
.gr-c-primary-nav__menu-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.gr-c-primary-nav__menu-item {
  margin: 0 0 1rem;
}
.gr-c-primary-nav__menu-item:last-child {
  margin: 0;
}
.gr-c-primary-nav__menu-link {
  color: #182962;
}
@media (min-width: 48em) {
  .gr-c-primary-nav__menu-inner {
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: repeat(auto-fill, minmax(47%, 1fr));
  }
}
@media (min-width: 64em) {
  .gr-c-primary-nav__menu-inner {
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: repeat(auto-fill, minmax(23%, 1fr));
  }
}

/* make "__menu" available for independent review -- requires using full class names when nesting */
.gr-c-primary-nav .gr-c-primary-nav__menu {
  transition: opacity 0.1s ease-in-out;
  position: absolute;
  height: 0;
  overflow: hidden;
  /* without this, the page ends up super tall -- not sure why, yet */
  top: 100%;
  left: -100%;
  opacity: 0;
  z-index: 1;
  /* this should be top-most value */
}
.gr-c-primary-nav .gr-c-primary-nav__menu:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  right: -100%;
  left: -100%;
  background: #ebeced;
  border-bottom: 1px solid #ccc;
  z-index: -1;
}
.gr-c-primary-nav .gr-c-primary-nav__link--active ~ .gr-c-primary-nav__menu {
  height: auto;
  overflow: visible;
  left: 0;
  right: 0;
  opacity: 1;
}

