/*
 * checkbox
 */
.gr-c-checkbox [type="checkbox"]:not(:checked),
.gr-c-checkbox [type="checkbox"]:checked {
  position: absolute;
  left: -9999px;
}
.gr-c-checkbox [type="checkbox"]:not(:checked) + label,
.gr-c-checkbox [type="checkbox"]:checked + label {
  position: relative;
  padding-left: 1.95em;
  cursor: pointer;
}
.gr-c-checkbox [type="checkbox"]:not(:checked) + label:before,
.gr-c-checkbox [type="checkbox"]:checked + label:before {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
  border-style: solid;
  border-width: 1px;
  content: '';
  position: absolute;
  left: 0;
  top: .1rem;
  /* magic number */
  width: 1.25em;
  /* magic number */
  height: 1.25em;
  /* magic number */
  background: #fff;
}
.gr-c-checkbox [type="checkbox"]:not(:checked) + label:after,
.gr-c-checkbox [type="checkbox"]:checked + label:after {
  position: absolute;
  top: -.3em;
  /* magic number */
  left: .7em;
  /* magic number */
  content: '';
  display: block;
  width: 9px;
  height: 20px;
  border-style: solid;
  border-color: #028abe;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
.gr-c-checkbox [type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0);
}
.gr-c-checkbox [type="checkbox"]:checked + label:after {
  opacity: 1;
}
.gr-c-checkbox [type="checkbox"]:disabled:checked + label:after {
  color: #ebeced;
  color: var(--gr-light-gray);
}
.gr-c-checkbox [type="checkbox"]:disabled:checked + label:before {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
}
.gr-c-checkbox [type="checkbox"]:disabled + label {
  color: #ebeced;
  color: var(--gr-light-gray);
}
.gr-c-checkbox--flipped {
  /* display checkmark after the text */
}
.gr-c-checkbox--flipped [type="checkbox"]:not(:checked) + label,
.gr-c-checkbox--flipped [type="checkbox"]:checked + label {
  padding-left: 0;
  padding-right: 1.95em;
}
.gr-c-checkbox--flipped [type="checkbox"]:not(:checked) + label:before,
.gr-c-checkbox--flipped [type="checkbox"]:checked + label:before {
  left: auto;
  right: 0;
}
.gr-c-checkbox--flipped [type="checkbox"]:not(:checked) + label:after,
.gr-c-checkbox--flipped [type="checkbox"]:checked + label:after {
  left: auto;
  /* override default */
  right: -.075em;
}
