.gr-c-mobile-nav .gr-c-search-box__text-input:hover, .gr-c-mobile-nav .gr-c-search-box__text-input:focus, .gr-c-mobile-nav .gr-c-search-box__text-input:focus:hover {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
}
.gr-c-mobile-nav .gr-c-search-box__btn {
  display: none;
}
.gr-c-mobile-nav ::placeholder {
  color: #979797;
  color: var(--gr-dark-gray);
}

/*
 * float label
 */
.field--wrapper {
  position: relative;
}
.field--wrapper label {
  position: absolute;
  top: -1.5rem;
  left: 0;
  font-size: 12px;
  color: #ccc;
  transition: all .1s linear;
  opacity: 0;
  font-weight: bold;
}
.field--wrapper label.on {
  color: #028abe;
}
.field--wrapper label.show {
  opacity: 1;
}

/*
 * video
 */
.gr-c-video {
  height: 0;
  position: relative;
  padding-top: 56.25%;
  /* 56.25% is used for '560 x 315' dimensions (YouTube videos) */
}
.gr-c-video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*
 * form
 */
.gr-c-form__item {
  margin: 0;
  /* overrides default 'fieldset' margin -- rely on 'grid-gap' instead */
}
.gr-c-form__item--fill {
  grid-column: span 3;
}
.gr-c-form__item--check {
  position: relative;
  padding-left: 1rem;
}
.gr-c-form__item--check input[type=checkbox] {
  position: absolute;
  left: 0;
  top: .4rem;
}
.gr-c-form__item--btn {
  text-align: right;
}
.gr-c-form--signup {
  display: grid;
  grid-gap: 1.5rem;
  grid-template-columns: inherit;
}
.gr-c-form--signup .gr-c-form__item--fill {
  grid-column: inherit;
}
@media (min-width: 30em) {
  .gr-c-form--signup {
    grid-template-columns: repeat(2, 1fr);
  }
  .gr-c-form--signup .gr-c-form__item--fill {
    grid-column: span 2;
  }
}
.gr-c-form--payment {
  display: grid;
  grid-gap: 1.5rem;
  grid-template-columns: inherit;
}
.gr-c-form--payment .gr-c-form__item--fill {
  grid-column: inherit;
}
@media (min-width: 30em) {
  .gr-c-form--payment {
    grid-template-columns: repeat(3, 1fr);
  }
  .gr-c-form--payment .gr-c-form__item--fill {
    grid-column: span 3;
  }
}

/*
 * modal
 */
.gr-c-modal {
  /* NOTE: customizations for modal that includes "photo block" carousel -- these might be changed to be default settings */
  /* NOTE: the purpose of the cusomizations is make sure hidden modal content can be viewed by scrolling the page */
}
.gr-c-modal__header {
  display: grid;
  grid-gap: 0;
  grid-template-columns: 1fr auto;
  border-color: #ccc;
  border-color: var(--gr-medium-gray);
  border-style: solid;
  border-width: 0 0 1px;
  padding: 0 0 1rem;
  margin: 0 0 1rem;
}
.gr-c-modal__dialog {
  transform: translate(0, -500%);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  -moz-transition: -moz-transform 0.3s ease-out;
  -o-transition: -o-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  background: #fff;
  padding: 1rem;
  width: 100%;
  max-width: 700px;
  /* magic number -- this should be in a variable? */
  margin-right: auto;
  margin-left: auto;
  position: fixed;
  top: -100%;
  z-index: 11;
  /* should this value be in a variable? */
  right: 0;
  left: 0;
}
.gr-c-modal__heading {
  margin: 0;
}
.gr-c-modal__close-btn {
  font-weight: bold;
  justify-self: end;
}
.gr-c-modal__close-btn:hover {
  text-decoration: none;
}
.gr-c-modal:before {
  /* modal background */
  content: "";
  display: none;
  background: rgba(0, 0, 0, 0.5);
  /* should this value be in a variable? */
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  /* should this value be in a variable? */
}
.gr-c-modal:target:before {
  display: block;
}
.gr-c-modal:target .gr-c-modal__dialog {
  transform: translate(0, 0);
  top: 20%;
  /* should this value be in a variable? */
}
.gr-c-modal--tall {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -999;
  /* NOTE: this value should be in a variable */
  /* NOTE: the "__background" element should replace the :before pseudo element -- markup edits/additions will be needed in dev */
}
.gr-c-modal--tall .modal__dialog {
  position: absolute;
}
.gr-c-modal--tall .modal__dialog--full-width {
  max-width: 815px;
}
.gr-c-modal--tall .modal__background {
  display: none;
  background: rgba(255, 165, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  cursor: default;
}
.gr-c-modal--tall:target {
  z-index: 999;
}
.gr-c-modal--tall:target .modal__dialog--full-width {
  top: 60px;
  /* NOTE: magic number -- mimics height of mobile header */
}
.gr-c-modal--tall:target:before {
  display: none;
  /* NOTE: this will ultimately be removed -- see note about "__background" element above */
}
.gr-c-modal--tall:target .modal__background {
  display: block;
}
@media (min-width: 75rem) and (min-height: 1024px) {
  .gr-c-modal--tall .modal__dialog--full-width {
    max-width: 75rem;
  }
}

/*
 * icon box
 */
.gr-c-icon-box {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
}
.gr-c-icon-box__item {
  margin-right: 1.5rem;
}
.gr-c-icon-box__item:last-child {
  margin-right: 0;
}
.gr-c-icon-box__link {
  color: #f47c2e;
  color: var(--gr-orange);
  font-size: 2rem;
}
.gr-c-icon-box__link:hover {
  text-decoration: none;
}

/*
 * nav menu
 * https://inclusive-components.design/tabbed-interfaces/
 */
.gr-c-tab-menu {
  background-color: #ccc;
  position: relative;
  z-index: 999;
  /* this value should be in a variable, and should be highest possible (must overlap everything else) */
}
.gr-c-tab-menu__inner {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  width: 100%;
  /* this appeared to be needed for Safari on iPhone X */
}
.gr-c-tab-menu__section {
  display: none;
  background-color: #ccc;
  flex: 0 0 100%;
  position: absolute;
  top: 49px;
  /* value matches height of "__tab" element */
  width: 100%;
  border-top: 2px solid #ccc;
}
.gr-c-tab-menu__input {
  display: none;
}
.gr-c-tab-menu__input:checked + label {
  color: #fff;
  background-color: #028abe;
  flex: 0 0 calc(50% - 1px);
}
.gr-c-tab-menu__tab {
  font-size: 14px;
  color: #979797;
  background-color: #ebeced;
  flex: 0 0 calc(50% - 1px);
  width: calc(50% - 1px);
  text-align: center;
  padding: 0 1rem;
  /* rely on line-height to control height of element */
  line-height: 49px;
  /* value should match "__section" element */
  margin: 0 1px 0 0;
}
.gr-c-tab-menu__tab:last-of-type {
  /* using "last of type" since there are hidden elements in between labels (inputs) */
  margin: 0 0 0 1px;
}
.gr-c-tab-menu__tab:hover {
  color: #979797;
  background-color: #ebeced;
  cursor: pointer;
}
.gr-c-tab-menu__icon {
  font-size: 1.4rem;
  vertical-align: middle;
}
.gr-c-tab-menu #tab1:checked ~ #content1,
.gr-c-tab-menu #tab2:checked ~ #content2 {
  display: block;
}
.gr-c-tab-menu .gr-c-btn-set--grid {
  grid-gap: 2px;
}
.gr-c-tab-menu .gr-c-btn {
  font-size: 14px;
  padding: 1rem;
  display: flex;
  justify-content: center;
}
.gr-l-template .gr-c-tab-menu, .gr-l-page .gr-c-tab-menu {
  margin: 0 0 1rem;
}

/*
 * page header
 */
.gr-c-page-header {
  position: relative;
  z-index: 2;
  /* this element should be higher than the "gr-l-main" element */
}

/*
 * utility / visibility
 */
[hidden],
.gr-u-hidden {
  display: none !important;
}

.gr-u-visible {
  display: block;
}

/*
 * NOTE: hide only visually, but have it available for screen readers:
 *       http://snook.ca/archives/html_and_css/hiding-content-for-accessibility
 */
/* NOTE: using both class names since they're both being used */
.gr-u-visually-hidden,
.gr-u-visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

/*
 * NOTE: extends the .visuallyhidden class to allow the element to be focusable when navigated to via the keyboard:
 *       https://www.drupal.org/node/897638
 */
.gr-u-visuallyhidden.focusable:active,
.gr-u-visuallyhidden.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

/*
 * NOTE: hide visually and from screen readers, but maintain layout
 */
.gr-u-invisible {
  visibility: hidden;
}

/*
 * utility
 */
.gr-u-nobr {
  white-space: nowrap;
}

.gr-u-text-center {
  text-align: center;
}

.gr-u-no-text-transform {
  text-transform: none;
}

.gr-u-text-left {
  text-align: left;
  /* NOTE: override rules that affect alignment */
}
table td .gr-u-text-left {
  text-align: left;
}

.gr-u-text-right {
  text-align: right;
}

.gr-u-float-right {
  float: right;
}

.gr-u-text-no-wrap {
  white-space: nowrap;
}

.gr-u-no-top-margin {
  margin-top: 0;
}

/*
 * background color
 */
.gr-u-bg-primary-blue {
  background-color: #028abe;
  background-color: var(--gr-primary-blue);
}

.gr-u-bg-dark-blue {
  background-color: #182962;
  background-color: var(--gr-dark-blue);
}

.gr-u-bg-light-orange {
  background-color: #ffb180;
  background-color: var(--gr-light-orange);
}

.gr-u-bg-orange {
  background-color: #f47c2e;
  background-color: var(--gr-orange);
}

.gr-u-bg-dark-orange {
  background-color: #ff6400;
  background-color: var(--gr-dark-orange);
}

.gr-u-bg-red {
  background-color: #ce0000;
  background-color: var(--gr-red);
}

.gr-u-bg-green {
  background-color: #98cb4e;
  background-color: var(--gr-green);
}

.gr-u-bg-cool-gray {
  background-color: #ebeced;
  background-color: var(--gr-light-gray);
}

.gr-u-bg-medium-gray {
  background-color: #ccc;
  background-color: var(--gr-medium-gray);
}

.gr-u-bg-dark-gray {
  background-color: #979797;
  background-color: var(--gr-dark-gray);
}

.gr-u-bg-black {
  background-color: #000;
  background-color: var(--gr-black);
}

.gr-u-bg-white {
  background-color: #fff;
  background-color: var(--gr-white);
}

/*
 * text color
 */
.gr-u-primary-blue {
  color: #028abe;
  color: var(--gr-primary-blue);
}

.gr-u-dark-blue {
  color: #182962;
  color: var(--gr-dark-blue);
}

.gr-u-light-orange {
  color: #ffb180;
  color: var(--gr-light-orange);
}

.gr-u-orange {
  color: #f47c2e;
  color: var(--gr-orange);
}

.gr-u-dark-orange {
  color: #ff6400;
  color: var(--gr-dark-orange);
}

.gr-u-red {
  color: #ce0000;
  color: var(--gr-red);
}

.gr-u-green {
  color: #98cb4e;
  color: var(--gr-green);
}

.gr-u-cool-gray {
  color: #ebeced;
  color: var(--gr-light-gray);
}

.gr-u-medium-gray {
  color: #ccc;
  color: var(--gr-medium-gray);
}

.gr-u-dark-gray {
  color: #979797;
  color: var(--gr-dark-gray);
}

.gr-u-black {
  color: #000;
  color: var(--gr-black);
}

.gr-u-white {
  color: #fff;
  color: var(--gr-white);
}

/*
 * display
 */
.gr-u-display-contents {
  display: contents;
}

/* show/hide based on viewport width */
.gr-u-display--medium {
  display: none;
}
@media (min-width: 48em) {
  .gr-u-display--medium {
    display: block;
  }
}
@media (min-width: 48em) {
  .gr-u-display--small-only {
    display: none;
  }
}
@media (min-width: 64em) {
  .gr-u-display--medium-only {
    display: none;
  }
}
.gr-u-display--large-only {
  display: none;
}
@media (min-width: 64em) {
  .gr-u-display--large-only {
    display: block;
  }
}
@media (min-width: 64em) {
  .gr-u-display--small-medium-only {
    display: none;
  }
}

/*
 * font
 */
.gr-u-f-primary {
  font: normal normal 400 100%/1.5 "Open Sans", sans-serif;
}

.gr-u-f-primary-light {
  font: normal normal 300 100%/1.5 "Open Sans", sans-serif;
}

.gr-u-f-primary-bold {
  font: normal normal 700 100%/1.5 "Open Sans", sans-serif;
}

.gr-u-f-primary-italic {
  font: italic normal 400 100%/1.5 "Open Sans", sans-serif;
}

.gr-u-f-secondary {
