}

/*
 * text
 */
.gr-u-italic {
  font-style: italic;
}

.gr-u-bold {
  font-weight: bold;
}

/*
 * palette
 */
.gr-pl-o-palette {
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  /* add margin below text inputs when viewing as "atoms" */
}
.gr-pl-o-palette__box {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
  border-style: solid;
  border-width: 1px;
  height: 100%;
  /* gives consistent visual heights, specifically for <textarea> demo */
  padding: 1rem;
}
.gr-pl-o-palette__box--heading {
  padding: 0;
  border: 0;
}
.gr-pl-o-palette__swatch--color {
  height: 0;
  padding-top: 100%;
  margin: 0 0 1rem;
}
.gr-pl-o-palette__swatch--color.gr-u-bg-white {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
  border-style: solid;
  border-width: 1px;
}
.gr-pl-o-palette__swatch--font {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
  border-style: solid;
  border-width: 0 0 1px;
  margin-bottom: 1rem;
  overflow-x: auto;
}
.gr-pl-o-palette__swatch--button {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
  border-style: solid;
  border-width: 0 0 1px;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}
.gr-pl-o-palette__swatch--heading {
  margin-bottom: 1rem;
}
.gr-pl-o-palette__swatch--icon {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
  border-style: solid;
  border-width: 0 0 1px;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}
.gr-pl-o-palette__swatch--form-element {
  border-color: #ebeced;
  border-color: var(--gr-light-gray);
  border-style: solid;
  border-width: 0 0 1px;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}
.gr-pl-o-palette__heading {
  font-weight: bold;
  white-space: nowrap;
}
.gr-pl-o-palette__heading--big {
  font-weight: normal;
  font-size: 2rem;
  margin: 0 0 1rem;
  /* this class is used on a <p> element for font palette -- might want to change to heading or list markup */
}
.gr-pl-o-palette__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.gr-pl-o-palette__list-item {
  margin: 0 0 .25rem;
}
.gr-pl-o-palette__list-item--heading {
  font-weight: bold;
}
.gr-pl-o-palette__list-item:last-child {
  margin: 0;
}
.gr-pl-o-palette--fonts {
  /* adjust grid item width so more of alphabet is visible */
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
}
@media (min-width: 40em) {
  .gr-pl-o-palette--fonts {
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}
.gr-pl-o-palette--buttons {
  /* adjust grid item width so CSS classes remain on one line on wider screens */
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
}
@media (min-width: 48em) {
  .gr-pl-o-palette--buttons {
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
  }
}
.gr-pl-o-palette--headings {
  display: block;
  /* when viewing heading palette, use same margin value for all headings -- removes unnecessary whitepsace */
}
.gr-pl-o-palette--headings .gr-pl-o-palette__item {
  margin: 0 0 2rem;
}
.gr-pl-o-palette--headings .gr-pl-o-palette__item:last-child {
  margin: 0;
}
.gr-pl-o-palette--headings .gr-c-heading {
  margin: 0 0 1rem;
}
.gr-pl-o-palette--icons {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(190px, 1fr));
}
.gr-pl-o-palette--form-elements {
  /*  */
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
}
@media (min-width: 18.75em) {
  .gr-pl-o-palette--form-elements {
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  }
}
.gr-pl-o-palette .gr-c-input-text {
  margin: 0 0 1rem;
}

